.bc-button {
  background: linear-gradient(-45deg, #000 0%, #177349 100%);
  min-height: 54px;
  min-width: 200px;
  border-radius: 27px;
  border: none;
  color: #fff;
  transition-duration: 0.1s;
  transition-property: all;
  position: relative;
  overflow: hidden;
}
.bc-button span {
  position: relative;
  z-index: 2;
}
.bc-button:before {
  opacity: 0;
  height: 100%;
  width: 100%;
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  background: linear-gradient(45deg, #000 0%, #177349 100%);
  transition-duration: 3s;
  transition-property: all;
}
.bc-button:hover:before {
  opacity: 1;
}
